import React, { Component, Fragment } from 'react';

import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles'
import Divider from '@material-ui/core/Divider';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import Typography from '@material-ui/core/Typography';
import appText from '../../../data/appText';

const styles = theme => ({
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    },
    card: { 
        minHeight: '350px'
    },
    addieTitle: {
        paddingBottom: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        borderBottom: '3px solid #4e89eb'
    },
    addieA: {
        borderColor: '#4dabf5'
    },
    addieD1: {
        borderColor: '#5393ff'
    },
    addieD2: {
        borderColor: '#2196f3'
    },
    addieI: {
        borderColor: '#2979ff'
    },
    addieE: {
        borderColor: '#1769aa'
    },
    addieT: {
        borderColor: '#ff5722'
    },
    total: {
        fontWeight: 'bold'
    },
    big: {
        fontSize: '1rem'
    },
    numberCell: {
        textAlign: 'right'
    }
})

class CAI extends Component {
    constructor(props) {
        super(props);
        this.data = {
            ...this.props.data.shared,
            ...this.props.data.cai,
            totalTDYCost: 0,
            instPer25Students: 0,
            instTeaching40weeks1: 0,
            instTeaching40weeks2: 0,
            instTDY3Years: 0,
            instAnnualCost: 0,
            learnerTDYCost: 0,
            instCostPerAnnualLearner: 0,
            costOneClassroom: 0,
        };
    }

    calcAndSetData() {
        let workingData = { ...this.data, ...this.props.data.shared, ...this.props.data.cai };

        let calculations = {
            instPer25Students: workingData.learnerTargetAudience / workingData.instructorsRequired,
            instTeaching40weeks1: (workingData.learnerTargetAudience / 25) / 20,
            instTeaching40weeks2: (workingData.learnerTargetAudience / 25) / 40,
            instTDY3Years: ((workingData.learnerTargetAudience / 25) / 20) / 3,
            instAnnualCost: (((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost,
            instCostPerAnnualLearner: 
                ((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                / workingData.learnerTargetAudience / 3,
            costOneClassroom: workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired,
            implementationCostPerLearner: 
                workingData.learnerTDYCost
                + (((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                    / workingData.learnerTargetAudience / 3)
                + (workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired),
            athdCost: Number(workingData.caiAthdCost),
            athdLicenseCost: Number(workingData.caiAthdLicenseCost),
            tktHours: workingData.caiTktHours,
            EvaluationCostPerLearner: (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)) / workingData.learnerTargetAudience,
            totalCost: 
                ((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost)
                + (workingData.learnerTargetAudience * workingData.learnerTDYCost)
                + (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)),
            totalCostPerLearner:
                workingData.learnerTDYCost
                + (((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                    / workingData.learnerTargetAudience / 3)
                + (workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired)
                + (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)) / workingData.learnerTargetAudience
        }
        
        this.data = { ...workingData, ...calculations }
    }

    render() {
        const { classes } = this.props;
        this.calcAndSetData();
        return (
            <Fragment>
                <Typography variant="h4" gutterBottom>Computer-Assisted Instruction (CAI)</Typography>
                <Typography variant="body1"gutterBottom>Distributed learning. Content is scored by an instructor, and the learner and instructor are in different classrooms.</Typography>
                <Divider className={classes.divider} />

                <Typography variant="h5" gutterBottom>Costs by ADDIE Stage</Typography>

                <Grid container spacing={8}>
                    <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieA].join(" ")}>
                                        Analysis
                                    </Typography>
                                    <Typography varient="body1">
                                        {appText.misc.analysis.text}
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieD1].join(" ")}>
                                        Design
                                    </Typography>
                                    <Typography variant="body1">
                                        {appText.misc.design.text}
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieD2].join(" ")}>
                                        Development
                                    </Typography>
                                    <Typography variant="body1">
                                        {appText.misc.development.text}
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieI].join(" ")}>
                                        Implementation
                                    </Typography>
                                    <Table>
                                        <TableBody>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.totalLearnerTDYCost.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.totalTDYCost.toFixed(2)}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.instructorsRequired.label}</TableCell>
                                                <TableCell className={classes.numberCell}>{Math.round(this.data.instPer25Students).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.instructors40Weeks.label}</TableCell>
                                                <TableCell className={classes.numberCell}>{Math.round(this.data.instTeaching40weeks1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            {/* <TableRow>
                                                <TableCell># Instructors Teaching 40 Weeks a Year (40 1-week classes)</TableCell>
                                                <TableCell className={classes.numberCell}>{Math.round(this.data.instTeaching40weeks2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow> */}
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.instructorCost3Years.label}</TableCell>
                                                <TableCell className={classes.numberCell}>{Math.round(this.data.instTDY3Years).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.annualInstructorCost.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.instAnnualCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.tdyCostPerLearner.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.learnerTDYCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.instructorCost3Years.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.instCostPerAnnualLearner.toFixed(2)}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.classroomCostPerLearner.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.costOneClassroom.toFixed(2)}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.caiAndLive.implementation.costPerLearner.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.implementationCostPerLearner.toFixed(2)}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                    </Table>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieE].join(" ")}>
                                        Evaluation
                                    </Typography>
                                    <Table>
                                        <TableBody>
                                            <TableRow>
                                                <TableCell>{appText.shared.fields.evaluation.helpDeskCost.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.athdCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.shared.fields.evaluation.helpDeskLicenseCost.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.athdLicenseCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.shared.fields.evaluation.tktHours.label}</TableCell>
                                                <TableCell className={classes.numberCell}>{this.data.tktHours.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell>{appText.cmi.evaluation.costPerLearner.label}</TableCell>
                                                <TableCell className={classes.numberCell}>${this.data.EvaluationCostPerLearner.toFixed(4)}</TableCell>
                                            </TableRow>
                                        </TableBody>
                                    </Table>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h6" className={[classes.addieTitle, classes.addieT].join(" ")}>
                                        Totals
                                    </Typography>
                                    <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell className={[classes.total, classes.big].join(" ")}>
                                                {appText.cai.totals.cost.label}
                                            </TableCell>
                                            <TableCell numeric className={[classes.numberCell, classes.total, classes.big].join(" ")}>
                                                ${this.data.totalCost.toFixed(0).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell className={classes.total}>
                                                {appText.cai.totals.costPerLearner.label}
                                            </TableCell>
                                            <TableCell numeric className={[classes.numberCell, classes.total].join(" ")}>
                                                ${this.data.totalCostPerLearner.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
            </Fragment>
        );
    }
}

export default withStyles(styles)(CAI);